import React from 'react';
import { Modal, ModalHeader, ModalBody, ModalFooter } from 'reactstrap';
import { getProducts, editOrder } from '../../lib/Api';

import './OrderEdit.css';

export default class OrderEdit extends React.Component {
    constructor(props) {
        super(props);
        this.editOrder = this.editOrder.bind(this);
        this.handleQuantity = this.handleQuantity.bind(this);
        this.handleCode = this.handleCode.bind(this);
        this.initOrderDetails = this.initOrderDetails.bind(this);
        this.handleStatus = this.handleStatus.bind(this);
        this.handleDiscountChange = this.handleDiscountChange.bind(this);
        this.handleDiscountAmount = this.handleDiscountAmount.bind(this);
        this.handleDiscountRemove = this.handleDiscountRemove.bind(this);
        this.handleSurveySent = this.handleSurveySent.bind(this);
        this.handleSurveyResponse = this.handleSurveyResponse.bind(this);
        this.state = {
            products: [],
            orderDetails: [],
            status: '',
            checked: false,
            discountAmount: '0',
            checkedDiscountRemove: false,
            surveySent: '',
            surveyResponse: ''
        }
    }

    componentDidMount() {
        getProducts().then(res => {
            if (res.data != null && res.data.products.length >= 1) {
                this.setState({ products: res.data.products });
            }
        }).catch(err => {
            console.log('err: ', err);
        });
    }

    editOrder() {
        let editedOrderDetails = [];
        if (this.state.orderDetails.length === 0) {
            for (let i = 0; i < this.props.order.orderDetails.length; i++) {
                editedOrderDetails[i] = { quantity: this.props.order.orderDetails[i].quantity, code: this.props.order.orderDetails[i].code }
            }
        } else {
            editedOrderDetails = this.state.orderDetails;
            for (let i = 0; i < editedOrderDetails.length; i++) {
                if (editedOrderDetails[i].quantity === '' || editedOrderDetails[i].quantity == null) {
                    editedOrderDetails[i].quantity = this.props.order.orderDetails[i].quantity;
                }
                if (editedOrderDetails[i].code === '' || editedOrderDetails[i].code == null) {
                    editedOrderDetails[i].code = this.props.order.orderDetails[i].code;
                }
            }
        }

        let discountPercentage;
        if (this.state.checkedDiscountRemove) {
            discountPercentage = '0';
        } else if (this.state.checked) {
            discountPercentage = this.state.discountAmount;
        } else {
            discountPercentage = this.props.order.discountPercentage;
        }


        let editOrderBody = {
            preOrderDetails: this.props.order.orderDetails,
            orderDetails: editedOrderDetails,
            status: this.state.status === ''? this.props.order.status : this.state.status,
            orderId: this.props.order._id,
            discountPercentage,
            surveySent: this.state.surveySent === '' ? this.props.order.surveySent : this.state.surveySent,
            surveyResponse: this.state.surveyResponse === '' ? this.props.order.surveyResponse : this.state.surveyResponse
        }

        if (editOrderBody['status'] === 'delivered') {
            editOrderBody['deliveryDate'] = this.props.order.deliveryDate ? this.props.order.deliveryDate : Date(Date.now);
        } else {
            editOrderBody['deliveryDate'] = '';
        }
        editOrder(editOrderBody).then(res => {
            editOrderBody['price'] = res.data.order.price;
            this.props.updateOrder(editOrderBody);
            this.props.closeModal();
            this.setState({
                orderDetails: [],
                status: '',
                checked: false,
                checkedDiscountRemove: false,
                surveySent: '',
                surveyResponse: ''
            });
        }, err => {
            console.log(err);
            console.log(err.response);
        })
    }

    initOrderDetails(index, event, id) {
        let ordersSize = this.props.order.orderDetails.length;
        let newOrderDetails = []
        for (let i = 0; i < ordersSize; i++) {
            newOrderDetails[i] = { quantity: this.props.order.orderDetails[i].quantity, code: this.props.order.orderDetails[i].code }
        };
        if (id === 'quantity') {
            let newQuantity = Number(event.target.value);
            this.setState(prevState => ({
                orderDetails: newOrderDetails.map((orderDetail, pointer) => {
                    return pointer === index? {...orderDetail, quantity: newQuantity} : orderDetail;
                })
            }));
        } else {
            let newCode = event.target.value;
            this.setState(prevState => ({
                orderDetails: newOrderDetails.map((orderDetail, pointer) => {
                    return pointer === index? {...orderDetail, code: newCode} : orderDetail;
                })
            }));
        }
    }

    handleQuantity(i, event) {
        if (this.state.orderDetails.length === 0) {
            this.initOrderDetails(i, event, 'quantity');
        } else {
            this.handleQuantityHelper(i, event.target.value);
        }
    }

    handleQuantityHelper(i, newQuantity) {
        this.setState(prevState => ({
            orderDetails: prevState.orderDetails.map((orderDetail, index) => {
                return index === i? { ...orderDetail, quantity: Number(newQuantity) } : orderDetail;
            })
        }));
    }

    handleCode(i, event) {
        if (this.state.orderDetails.length === 0) {
            this.initOrderDetails(i, event, 'code');
        } else {
            this.handleCodeHelper(i, event.target.value);
        }
    }

    handleCodeHelper(i, newCode) {
        this.setState(prevState => ({
            orderDetails: prevState.orderDetails.map((orderDetail, index) => {
                return index === i? { ...orderDetail, code: newCode } : orderDetail;
            })
        }));
    }

    handleStatus(event) {
        if (event.target.value === 'delivered') {
            this.setState({ status: event.target.value });    
        } else {
            this.setState({ status: event.target.value });
        }
    }

    handleDiscountChange() {
        this.setState({ checked: !this.state.checked, checkedDiscountRemove: false });
    }

    handleDiscountAmount(event) {
        this.setState({ discountAmount: event.target.value });
    }

    handleDiscountRemove() {
        this.setState({ checkedDiscountRemove: !this.state.checkedDiscountRemove, checked: false });
    }

    handleSurveySent() {
        this.setState({ surveySent: !this.state.surveySent });
    }

    handleSurveyResponse() {
        this.setState({ surveyResponse: !this.state.surveyResponse });
    }

    render() {
        if (this.props.order.orderDetails) {
            const applyDiscount = this.state.checked 
                ? <div className="form-group">
                    <div className="form-group">
                        <label>Discount:</label>
                        <input type="number" className="form-control" id="discount" placeholder="Discount Percentage" onChange={this.handleDiscountAmount} />
                    </div>
                  </div>
                : null;
            const removeDiscount = this.props.order.discountPercentage !== "0"
                ? <div className="form-group">
                    <div className="custom-control custom-checkbox">
                        <input className="custom-control-input" id="removeDiscountCheck" type="checkbox" checked={ this.state.checkedDiscountRemove } onChange={ this.handleDiscountRemove } />
                        <label className="custom-control-label" htmlFor="removeDiscountCheck">Remove discount</label>
                    </div>
                    <hr />
                </div>
                : null;
            return(
                <div>
                    { (this.props.showModal) ? 
                        <Modal isOpen={this.props.showModal}>
                            <ModalHeader>Existing Order</ModalHeader>
                            <ModalBody>
                                <form>
                                    {
                                        this.props.order.orderDetails.map((orderDetail, index) => {
                                            return(
                                                <div className="row" key={index}>
                                                    <div className="col-md-3">Sub-order { index + 1 }:</div>
                                                    <div className="form-group col-md-4">
                                                        <select className="custom-select" value={this.state.orderDetails[index]? this.state.orderDetails[index].quantity : orderDetail.quantity} onChange={(event) => this.handleQuantity(index, event)}>
                                                            <option>1</option>
                                                            <option>2</option>
                                                            <option>3</option>
                                                            <option>4</option>
                                                            <option>5</option>
                                                            <option>6</option>
                                                            <option>7</option>
                                                            <option>8</option>
                                                            <option>9</option>
                                                        </select>
                                                    </div>
                                                    <div className="form-group col-md-4">
                                                        <select className="custom-select" value={this.state.orderDetails[index]? this.state.orderDetails[index].code : orderDetail.code} onChange={(event) => this.handleCode(index, event)}>
                                                            {
                                                                this.state.products.map((product, i) => {
                                                                    return <option key={i}>{product.code}</option>
                                                                })
                                                            }
                                                        </select>
                                                    </div>
                                                </div>
                                            )
                                        })
                                    }
                                    <hr />
                                    <div className="row">
                                        <div className="col-md-3">Status:</div>
                                        <div className="col-md-4 form-group">
                                            <select className="custom-select" value={this.state.status === ''? this.props.order.status : this.state.status} onChange={(event) => this.handleStatus(event)}>
                                                <option value="pending">pending</option>
                                                <option value="delivered">delivered</option>
                                            </select>
                                        </div>
                                    </div>
                                    <hr />
                                    <div className="form-group">
                                        <div className="custom-control custom-checkbox">
                                            <input className="custom-control-input" id="customCheck" type="checkbox" checked={ this.state.checked } onChange={ this.handleDiscountChange } />
                                            <label className="custom-control-label" htmlFor="customCheck">Apply discount</label>
                                        </div>
                                    </div>
                                    { applyDiscount }
                                    <hr />
                                    { removeDiscount }
                                    <div className="custom-control custom-checkbox">
                                        <input className="custom-control-input" id="surveySent" type="checkbox" checked={ this.state.surveySent === '' ? this.props.order.surveySent : this.state.surveySent } onChange={ this.handleSurveySent } />
                                        <label className="custom-control-label" htmlFor="surveySent">Survey Sent</label>
                                    </div>
                                    <hr />
                                    <div className="custom-control custom-checkbox">
                                        <input className="custom-control-input" id="surveyResponse" type="checkbox" checked={ this.state.surveyResponse === '' ? this.props.order.surveyResponse : this.state.surveyResponse } onChange={ this.handleSurveyResponse } />
                                        <label className="custom-control-label" htmlFor="surveyResponse">Survey Response</label>
                                    </div>
                                </form>
                            </ModalBody>
                            <ModalFooter>
                                <button type="submit" className="btn btn-info" onClick={this.editOrder}>Edit</button>
                                <button className="btn btn-danger" onClick={(event) => this.props.closeModal(event)}>Cancel</button>
                            </ModalFooter>
                        </Modal>
                    : null }
                </div>
            );
        } else {
            return null;
        }
       
    }
}