import React from 'react';
import { Modal, ModalHeader, ModalBody, ModalFooter } from 'reactstrap';
import { editUser } from '../../lib/Api';

import './UserEdit.css';

export default class UserEdit extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            name: 'default',
            email: 'default',
            instagramUsername: 'default',
            phone: 'default',
            address: 'default',
            errMsg: '',
            hideAlert: true
        }
    }

    handleName = (e) => {
        this.setState({ name: e.target.value });
    }

    handleInstagramUsername = (e) => {
        this.setState({ instagramUsername: e.target.value });
    }

    handleEmail = (e) => {
        this.setState({ email: e.target.value });
    }

    handlePhone = (e) => {
        this.setState({ phone: e.target.value });
    }

    handleAddress = (e) => {
        this.setState({ address: e.target.value });
    }

    editUser = () => {
        if (this.state.name === '' || this.state.email === '' || this.state.instagramUsername === '' || this.state.phone === '' || this.state.address === '') {
            this.setState({ errMsg: 'all fields should be filled', hideAlert: false }, () => {
                window.setTimeout(()=>{
                  this.setState({ errMsg: '', hideAlert: true })
                }, 4000)
            });
        } else if (!this.state.phone.match(/^\+[0-9]{12,13}$/g)) {
            this.setState({ errMsg: 'phone number must start with "+", followed by 12-13 digits', hideAlert: false }, () => {
                window.setTimeout(()=>{
                  this.setState({ errMsg: '', hideAlert: true })
                }, 4000)
            });
        } else {
            const editUserBody = {
                userId: this.props.user._id,
                name: this.state.name === 'default'? this.props.user.name : this.state.name,
                email: this.state.email === 'default'? this.props.user.email : this.state.email,
                instagramUsername: this.state.instagramUsername === 'default'? this.props.user.instagramUsername : this.state.instagramUsername,
                phone: this.state.phone === 'default'? this.props.user.phone : this.state.phone,
                address: this.state.address === 'default'? this.props.user.address : this.state.address
            }
            editUser(editUserBody).then(res => {
                this.props.getUsers();
                this.props.closeEditModal();
            }).catch(err => {
                this.setState({ errMsg: err.response.data, hideAlert: false }, () => {
                    window.setTimeout(()=>{
                      this.setState({ errMsg: '', hideAlert: true })
                    }, 4000)
                });
            })
        }
    }

    render() {
        return(
                <Modal isOpen={this.props.showModalEdit}>
                    <ModalHeader>Existing User</ModalHeader>
                    <ModalBody>
                        <div className="alert alert-danger" role="alert" hidden={this.state.hideAlert}>
                            { this.state.errMsg }
                        </div>
                        <div className="form-group">
                            <div className="form-group">
                                <label>Name:</label>
                                <input type="text" className="form-control" id="name" placeholder="Enter Your Name" onChange={this.handleName} value={this.state.name === 'default'? this.props.user.name : this.state.name} />
                            </div>
                        </div>
                        <div className="form-group">
                            <div className="form-group">
                                <label>Instagram Username:</label>
                                <input type="text" className="form-control" id="instagramUsername" placeholder="Enter Your Instagram Username" onChange={this.handleInstagramUsername} value={this.state.instagramUsername === 'default'? this.props.user.instagramUsername : this.state.instagramUsername} />
                            </div>
                        </div>
                        <div className="form-group">
                            <div className="form-group">
                                <label>Email:</label>
                                <input type="text" className="form-control" id="email" placeholder="Enter Your Email" onChange={this.handleEmail} value={this.state.email === 'default'? this.props.user.email : this.state.email} />
                            </div>
                        </div>
                        <div className="form-group">
                            <div className="form-group">
                                <label>Phone:</label>
                                <input type="text" className="form-control" id="phone" placeholder="Enter Your Phone" onChange={this.handlePhone} value={this.state.phone === 'default'? this.props.user.phone : this.state.phone} />
                            </div>
                        </div>
                        <div className="form-group">
                            <div className="form-group">
                                <label>Address:</label>
                                <input type="text" className="form-control" id="address" placeholder="Enter Your Address" onChange={this.handleAddress} value={this.state.address === 'default'? this.props.user.address : this.state.address} />
                            </div>
                        </div>
                    </ModalBody>
                    <ModalFooter>
                        <button type="submit" className="btn btn-info" onClick={this.editUser}>Edit</button>
                        <button className="btn btn-danger" onClick={(event) => this.props.closeEditModal(event)}>Cancel</button>
                    </ModalFooter>
                </Modal>
        );
    }

}