import React from 'react';
import Header from '../Header/Header';
import { getOrders, getProducts, getUsers } from '../../lib/Api';

import './Analytics.css';
import AnalyticsSocks from './AnaylticsSocks';

export default class Analytics extends React.Component {

    constructor(props) {
        super(props);
        this.closeModal = this.closeModal.bind(this);
        this.state = {
            usersCount: 0,
            ordersCount: 0,
            productsCount: [],
            totalProducts: 0,
            modalOpen: false
        }
    }

    componentDidMount() {
        getUsers('true').then(res => {
            this.setState({ usersCount: res.data.count });
        });

        getOrders('true').then(res => {
            this.setState({ ordersCount: res.data.count });
        });

        getProducts('true').then(res => {
            this.setState({ productsCount: res.data.productsCount });
            let count = 0;
            for (let i = 0; i < res.data.productsCount.length; i++) {
                count += Number(res.data.productsCount[i].count);
            }
            this.setState({ totalProducts: count });
        });
    }

    closeModal() {
        this.setState({ modalOpen: false });
    }

    render() {
        return(
            <div>
                <Header></Header>
                <div className="row">
                    <div className="col-md-4 analytics__container">
                        <div className="analytics__content">
                            <b>Customers</b> <br />
                            {this.state.usersCount}
                        </div>
                    </div>
                    <div className="col-md-4 analytics__container">
                        <div className="analytics__content">
                            <b>Orders</b> <br />
                            {this.state.ordersCount}
                        </div>
                    </div>
                    <div className="col-md-4 analytics__container">
                        <div className="analytics__content analytics__content--pointer">
                            <b>Socks</b> <br />
                            <span onClick={() => this.setState({ modalOpen: true })}>
                                {this.state.totalProducts}
                            </span>
                            <div className="analytics__content--note">Click on the number for more details</div>
                        </div>
                    </div>
                </div>
                <AnalyticsSocks productsCount={this.state.productsCount} modalOpen={this.state.modalOpen} closeModal={this.closeModal} />
            </div>
        );
    }
}