import React from 'react';
import { Modal, ModalHeader, ModalBody, ModalFooter } from 'reactstrap';
import { editProduct } from '../../lib/Api';

export default class ProductEdit extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            name: 'default',
            price: 'default',
            quantity: 'default',
            availableToSell: 'default',
            hideAlert: true
        }
    }

    handleName = (e) => {
        this.setState({ name: e.target.value });
    }

    handlePrice = (e) => {
        this.setState({ price: e.target.value });
    }

    handleQuantity = (e) => {
        this.setState({ quantity: e.target.value });
    }

    handleAvailableToSell = (e) => {
        this.setState({ availableToSell: e.target.value });
    }

    editProduct = () => {
        if (this.state.name === '' || this.state.price === '' || this.state.quantity === '' || this.state.availableToSell === '') {
            this.setState({ errMsg: 'all fields should be filled', hideAlert: false }, () => {
                window.setTimeout(()=>{
                  this.setState({ errMsg: '', hideAlert: true })
                }, 4000)
            });
        } else {
            const editProductBody = {
                code: this.props.product.code,
                name: this.state.name === 'default'? this.props.product.name : this.state.name,
                price: this.state.price === 'default'? this.props.product.price : this.state.price,
                quantity: this.state.quantity === 'default'? this.props.product.quantity : this.state.quantity,
                availableToSell: this.state.availableToSell === 'default'? this.props.product.availableToSell : this.state.availableToSell,
            }
            editProduct(editProductBody).then(res => {
                this.props.getProducts();
                this.props.closeEditModal();
            }).catch(err => {
                this.setState({ errMsg: err.response.data, hideAlert: false }, () => {
                    window.setTimeout(()=>{
                      this.setState({ errMsg: '', hideAlert: true })
                    }, 4000)
                });
            })
        }
    }

    render() {
        return(
                <Modal isOpen={this.props.showModalEdit}>
                    <ModalHeader>Existing Product</ModalHeader>
                    <ModalBody>
                        <div className="alert alert-danger" role="alert" hidden={this.state.hideAlert}>
                            { this.state.errMsg }
                        </div>
                        <div className="form-group">
                            <div className="form-group">
                                <label>Name:</label>
                                <input type="text" className="form-control" id="name" placeholder="Enter Your Name" onChange={this.handleName} value={this.state.name === 'default'? this.props.product.name : this.state.name} />
                            </div>
                        </div>
                        <div className="form-group">
                            <div className="form-group">
                                <label>Price:</label>
                                <input type="number" className="form-control" id="price" placeholder="Enter Your Price" onChange={this.handlePrice} value={this.state.price === 'default'? this.props.product.price : this.state.price} />
                            </div>
                        </div>
                        <div className="form-group">
                            <div className="form-group">
                                <label>Quantity:</label>
                                <input type="number" className="form-control" id="quantity" placeholder="Enter Your Quantity" onChange={this.handleQuantity} value={this.state.quantity === 'default'? this.props.product.quantity : this.state.quantity} />
                            </div>
                        </div>
                        <div className="form-group">
                            <div className="form-group">
                                <label>Available To Sell:</label>
                                <input type="number" className="form-control" id="availableToSell" placeholder="Enter Your Available To Sell" onChange={this.handleAvailableToSell} value={this.state.availableToSell === 'default'? this.props.product.availableToSell : this.state.availableToSell} />
                            </div>
                        </div>
                    </ModalBody>
                    <ModalFooter>
                        <button type="submit" className="btn btn-info" onClick={this.editProduct}>Edit</button>
                        <button className="btn btn-danger" onClick={(event) => this.props.closeEditModal(event)}>Cancel</button>
                    </ModalFooter>
                </Modal>
        );
    }

}