import React from 'react';
import { getRevenue } from '../../lib/Api';
import Header from '../Header/Header';

import './Revenue.css';

export default class Revenue extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            total: ''
        }
    }

    componentDidMount() {
        getRevenue().then(res => {
            this.setState({ total: res.data.result[0].total });
        }).catch(err => {
            console.log('err: ', err);
        });
    }

    render() {
        return(
            <div>
                <Header></Header>
                <div className="row">
                    <div className="col-md-4 offset-md-4 revenue__container">
                        <div className="revenue__content">
                            <b>Revenue</b> <br />
                            {this.state.total} L.E.
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}