import React from 'react';
import { Link } from 'react-router-dom';
import { tokenExpired } from '../../lib/helper_functions';

import './Header.css';

export default class Header extends React.Component {
    constructor(props) {
        super(props);
        this.logout = this.logout.bind(this);
        this.state = {
            redirectToRevenue: false,
            redirectToDashboard: false
        };
    }

    logout() {
        localStorage.removeItem('token');
    }

    render() {
        if (tokenExpired() || localStorage.getItem('token') === '' || localStorage.getItem('token') === null) {
            return (
                <div className="header__main">
                    <nav className="navbar fixed-top navbar-light bg-info">
                        {/* <span className="navbar-brand" href="#!" style={{ color: '#fff' }}>Jawareb</span> */}
                        <div>
                            <ul>
                                <li>
                                    <Link to="/dashboard/orders" style={{ color: '#fff', textDecoration: 'none' }}>Jawareb</Link>
                                </li>
                            </ul>
                        </div>
                    </nav>
                </div>
                
            )
        } else {
            return (
                <div className="header__main">
                    <nav className="navbar fixed-top navbar-light bg-info">
                        <div>
                            <ul>
                                <li>
                                    <Link to="/dashboard/orders" style={{ color: '#fff', textDecoration: 'none' }}>Jawareb</Link>
                                </li>
                                <li>
                                    <Link to="/dashboard/orders" style={{ color: '#fff', textDecoration: 'none' }}>Dashboard</Link>
                                </li>
                                <li>
                                    <Link to="/analytics/revenue" style={{ color: '#fff', textDecoration: 'none' }}>Analytics</Link>
                                </li>
                            </ul>
                        </div>
                        <div>
                            <Link style={{ color: '#fff', textDecoration: 'none' }} to="/" onClick={this.logout}>Logout</Link>
                        </div>
                    </nav>
                </div>
                
            )
        }
    }
}