import React from 'react';
import { Modal, ModalHeader, ModalBody, ModalFooter } from 'reactstrap';

export default class AnalyticsSocks extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
        }
    }

    componentDidMount() {
    }

    render() {
        return(
            <div>
                <Modal isOpen={this.props.modalOpen}>
                    <ModalHeader>Socks Breakdown</ModalHeader>
                    <ModalBody>
                        <div className="row">
                            <div className="offset-lg-2 col-lg-8">
                                <table className="analytics__table table">
                                    <thead className="thead-light">
                                        <tr>
                                            <th>Name (Code)</th>
                                            <th>Count</th>
                                        </tr>
                                    </thead>
                                    {
                                        this.props.productsCount.map(product => {
                                            return <tr key={product.code}>
                                                <td>
                                                    { `${product.name} (${product.code})` }
                                                </td>
                                                <td>
                                                    { product.count }
                                                </td>
                                            </tr>
                                        })
                                    }
                                </table>
                            </div>
                        </div>
                    </ModalBody>
                    <ModalFooter>
                        <button className="btn btn-danger" onClick={() => this.props.closeModal()}>Close</button>
                    </ModalFooter>
                </Modal>
            </div>
        );
    }
}