import axios from 'axios';
import * as Constants from '../constants';

const getHeaders = () => {
    const headers = {
        headers: {
            'x-access-token': localStorage.getItem('token')
        }
    };
    return headers;  
}

export const getOrders = (meta) => {
    const headers = getHeaders();
    return axios.get(Constants.API_URL + `/order${meta === 'true' ? '?meta=true' : '' }`, headers);
};

export const insertOrder = (order) => {
    const headers = getHeaders();
    return axios.post(Constants.API_URL + '/order', order, headers)
};

export const deleteOrders = (idToDelete) => {
    const headers = getHeaders();
    return axios.delete(Constants.API_URL + '/order/' + idToDelete, headers);
}

export const editOrder = (editOrderBody) => {
    const headers = getHeaders();
    return axios.patch(Constants.API_URL + '/order', editOrderBody, headers);
}

