import React from 'react';
import { NavLink } from 'react-router-dom';
import Header from '../Header/Header';

import './Dashboard.css';

export default class Dashboard extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            activeStyle: { backgroundColor: '#555', color: '#fff' },
            style: { backgroundColor: '#e9ecef' }
        }
    }

    render() {
        return(
            <div>
                <Header></Header>
                <div className="dashboard__container">
                    <div className="btn-group btn-group-lg" role="group" aria-label="Basic example">
                        <NavLink className="btn" to={this.props.first} activeStyle={ this.state.activeStyle } style={ this.state.style }>
                            {this.props.first.split('/')[this.props.first.split('/').length - 1]}
                        </NavLink>
                        <NavLink className="btn" to={this.props.second} activeStyle={this.state.activeStyle} style={ this.state.style }>
                            {this.props.second.split('/')[this.props.second.split('/').length - 1]}
                        </NavLink>
                        <NavLink className="btn" to={this.props.third} activeStyle={this.state.activeStyle} style={ this.state.style }>
                            {this.props.third.split('/')[this.props.third.split('/').length - 1]}
                        </NavLink>
                    </div>
                </div>
            </div>
        );
    }
}