import React from 'react';
import { Modal, ModalHeader, ModalBody, ModalFooter } from 'reactstrap';
import { sendReceipt } from '../../lib/Api';

export default class Receipt extends React.Component {

    constructor(props) {
        super(props);
        this.sendEmail = this.sendEmail.bind(this);
        this.handleFees = this.handleFees.bind(this);
        this.state = {
            deliveryFees: -1,
            hideAlert: true,
            hideInfoAlert: true,
            errMsg: ''
        }
    }

    sendEmail() {
        if (this.state.deliveryFees != -1) {
            let emailBody = {
                deliveryFees: Number(this.state.deliveryFees),
                price: this.props.order.price,
                orderDetails: this.props.order.orderDetails,
                username: this.props.order.customerId
            }
            this.setState({ hideInfoAlert: false });
            sendReceipt(emailBody).then(res => {
                this.props.closeModal();
                this.setState({ deliveryFees: -1, hideAlert: true, errMsg: '', hideInfoAlert: true });
            }).catch(err => {
                this.setState({ hideAlert: false, errMsg: err, hideInfoAlert: true });
            })
        } else {
            this.setState({ hideAlert: false, errMsg: 'All fields should be filled' });
        }
    }

    handleFees(e) {
        this.setState({ deliveryFees: e.target.value });
    }

    render() {
        return(
            <div>
                <Modal isOpen={this.props.showEmailModal}>
                    <ModalHeader>Receipt via email</ModalHeader>
                    <ModalBody>
                    <div className="alert alert-danger" role="alert" hidden={this.state.hideAlert}>
                        { this.state.errMsg }
                    </div>
                    <div className="alert alert-info" role="alert" hidden={this.state.hideInfoAlert}>
                        Sending...
                    </div>
                    <div className="form-group">
                        <div className="form-group">
                            <label>Delivery Fees:</label>
                            <input type="number" className="form-control" id="deliveryFees" placeholder="Delivery Fees" onChange={this.handleFees} />
                        </div>
                    </div>
                    </ModalBody>
                    <ModalFooter>
                        <button className="btn btn-info" onClick={() => this.sendEmail()}>Send</button>
                    </ModalFooter>
                </Modal>
            </div>
        );
    }
}