import * as JWT from 'jwt-decode';

export const addDeleteCheckers = (items) => {
    for (let i = 0; i < items.length; i++) {
        items[i]['orderNumber'] = formulateOrderNumber(items[i]['placingDate']);
        items[i]['toDelete'] = false;
    }
    return items;
}

const formulateOrderNumber = (date) => {
    const dateSpliited = new Date(date).toLocaleDateString().split('/');
    const timeSpliited = new Date(date).toLocaleTimeString().split(' ')[0].split(":");
    let orderNumber = dateSpliited[0] + dateSpliited[1] + "-" + dateSpliited[2] + "-" + timeSpliited[0] + timeSpliited[1] + timeSpliited[2];
    return orderNumber;
}

export const tokenExpired = () => {
    if (localStorage.getItem('token') == null || localStorage.getItem('token') === '') {
        return true;
    }
    let decoded = JWT(localStorage.getItem('token'));
    if (decoded.exp * 1000 <= Date.now()) {
        return true;
    }
    return false;
}