import React from 'react';
import OrderEdit from '../OrderEdit/OrderEdit';
import OrderInsert from '../OrderInsert/OrderInsert';
import Paginator from '../Paginatior/Paginatior';
import Select from 'react-select';

import { getOrders, deleteOrders } from '../../lib/Api';
import { addDeleteCheckers } from '../../lib/helper_functions';

import './Order.css';
import Receipt from '../Receipt/Receipt';

export default class Order extends React.Component {
    constructor(props) {
        super(props);
        this.changeCheckboxValue = this.changeCheckboxValue.bind(this);
        this.deleteOrders = this.deleteOrders.bind(this);
        this.filterOrders = this.filterOrders.bind(this);
        this.editOrder = this.editOrder.bind(this);
        this.closeModal = this.closeModal.bind(this);
        this.insertOrder = this.insertOrder.bind(this);
        this.getAllOrders = this.getAllOrders.bind(this);
        this.handleOrdersShown = this.handleOrdersShown.bind(this);
        this.updateOrder = this.updateOrder.bind(this);
        this.checkAllDelete = this.checkAllDelete.bind(this);
        this.setOrdersShown = this.setOrdersShown.bind(this);
        this.sortOrders = this.sortOrders.bind(this);
        this.handleFilter = this.handleFilter.bind(this);
        this.handleFilterAttribute = this.handleFilterAttribute.bind(this);
        this.sendEmail = this.sendEmail.bind(this);
        this.closeEmailModal = this.closeEmailModal.bind(this);
        this.state = {
            orders: [],
            filteredOrders: [],
            showModal: false,
            showModalInsert: false,
            showEmailModal: false,
            orderToEdit: {},
            orderToSend: {},
            itemsPerPage: 7,
            activePage: 1,
            ordersShown: [],
            checkAll: false,
            sortType: false,
            hideAlert: true,
            errMsg: '',
            sortingDates: {
                placingDate: false,
                deliveryDate: false
            },
            filterValue: '',
            filterAttribute: 'price',
            filterAttributeOptions: [
                { label: 'Price', value: 'price' },
                { label: 'Status', value: 'status' },
                { label: 'Discount', value: 'discountPercentage' },
                { label: 'Customer', value: 'customerId' }
            ]
        }
    }

    getAllOrders() {
        getOrders().then(res => {
            if (res.data != null && res.data.orders.length >= 1) {
                this.setState({ orders: addDeleteCheckers(res.data.orders), filterOrders: addDeleteCheckers(res.data.orders) });
                this.setOrdersShown();
            }
        }).catch(err => {
            console.log('err: ', err);
        });
    }

    sortOrders(orders, sortFieldFlag, field) {
        this.setState(prevState => ({
            sortingDates: {
                ...prevState.sortingDates,
                [field]: sortFieldFlag
            }
        }));
        orders.sort(function(a,b){
            return sortFieldFlag? new Date(b[field]) - new Date(a[field]) : new Date(a[field]) - new Date(b[field]);
        });
        this.setState({ orders });
        this.setOrdersShown();
    }

    componentDidMount() {
        this.getAllOrders();
    }

    setOrdersShown(orders) {
        const indexOfLastThing = this.state.activePage * this.state.itemsPerPage;
        const indexOfFirstThing = indexOfLastThing - this.state.itemsPerPage;
        if (orders === undefined) {
            this.setState({ ordersShown: this.state.orders.slice(indexOfFirstThing, indexOfLastThing) });
        } else {
            this.setState({ ordersShown: orders.slice(indexOfFirstThing, indexOfLastThing) });
        }
    }

    filterOrders() {
        let orders = this.state.orders;
        let filteredOrders = orders.filter(order => {
            return !order['toDelete'];
        });
        const indexOfLastThing = this.state.activePage * this.state.itemsPerPage;
        const indexOfFirstThing = indexOfLastThing - this.state.itemsPerPage;
        this.setState({ orders: filteredOrders, ordersShown: filteredOrders.slice(indexOfFirstThing, indexOfLastThing) });
    }

    insertOrder() {
        this.setState({ showModalInsert: true });
    }

    deleteOrders() {
        const idsToDelete = [];
        this.state.orders.map((order) => {
            if (order['toDelete']) { 
                idsToDelete.push(order._id);
            }
        });
        for (let i = 0; i < idsToDelete.length; i++) {
            deleteOrders(idsToDelete[i]).then(res => {
                this.setState({ errMsg: `the order(s) selected are deleted successfully`, hideAlert: false }, () => {
                    window.setTimeout(()=>{
                      this.setState({ errMsg: '', hideAlert: true })
                    }, 4000)
                });
                this.filterOrders();
            }).catch(err => {
                console.log(err);
            });
        }
    }

    editOrder(order) {
        this.setState({ orderToEdit: order, showModal: true });
    }

    updateOrder(order) {
        this.setState(prevState => ({
            orders: prevState.orders.map((stateOrder) => {
                return order.orderId === stateOrder._id? {...stateOrder, orderDetails: order.orderDetails, price: order.price, status: order.status, deliveryDate: order.deliveryDate, discountPercentage: order.discountPercentage, surveyResponse: order.surveyResponse, surveySent: order.surveySent} : stateOrder;
            })
        }), () => {
            this.setOrdersShown()
        });
    }

    changeCheckboxValue(index, event) {
        if (index === -1) {
            this.setState(prevState => ({
                orders: prevState.orders.map((stateOrder) => {
                    stateOrder['toDelete'] = prevState.checkAll? false : true;
                    return stateOrder;
                }), checkAll: !prevState.checkAll
            }));
            
            return;
        } else if (this.state.activePage > 1) {
            index += ((this.state.activePage - 1) * this.state.itemsPerPage);
        }
        let orders = this.state.orders;
        orders[index]['toDelete'] = event.target.checked;
        this.setState({ orders });
    }

    closeModal() {
        this.setState({ showModal: false, showModalInsert: false });
    }

    handleOrdersShown(orders, activePage) {
        this.setState({ ordersShown: orders, activePage });
    }

    checkAllDelete() {
        let orders = this.state.orders;
        for (let i = 0; i < orders; i++) {
            if (!orders[i]['toDelete']) {
                return false;
            }
        }
        return true;
    }

    handleFilter(e) {
        this.setState({ filterValue: e.target.value });
        let newOrders = [];
        switch(this.state.filterAttribute) {
            case 'price':
            case 'discountPercentage':
                newOrders = this.state.orders.filter((order, index) => {
                    return order[this.state.filterAttribute] > e.target.value;
                });
                break;
            case 'customerId':
            case 'status':
                newOrders = this.state.orders.filter(order => {
                    return order[this.state.filterAttribute].includes(e.target.value);
                });
                break;
        }
        this.setState({ filteredOrders: newOrders });
        this.setOrdersShown(newOrders);
    }

    handleFilterAttribute(e) {
        this.setState({ filterAttribute: e.value, filterValue: ''});
        this.handleFilter({ target: { value: '' } });
        this.setOrdersShown();
    }

    sendEmail(order) {
        this.setState({ orderToSend: order, showEmailModal: true });
    }

    closeEmailModal() {
        this.setState({ showEmailModal: false });
    }

    render() {
        let { filterAttribute } = this.state;
        filterAttribute = filterAttribute.label;
        return(
            <div className="row orders__container">
                <div className="col-md-10 offset-md-1">
                    <div className="row">
                        <div className="col-lg-6">
                            <div className="orders__filters-container">
                                <div className="row">
                                    <div className="col-lg-5">
                                        <Select
                                            defaultValue={filterAttribute}
                                            value={filterAttribute}
                                            onChange={this.handleFilterAttribute}
                                            options={this.state.filterAttributeOptions}
                                            placeholder={`Select filter ...`}
                                        />
                                    </div>
                                    <div className="col-lg-5">
                                        <input type="text" className="form-control" id="filter" placeholder={`Filter by ${this.state.filterAttribute}`} onChange={this.handleFilter} value={this.state.filterValue} />
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="col-lg-6">
                            <div className="orders__btn-container">
                                <button className="btn btn-info orders__btn" onClick={this.insertOrder}>Insert</button>
                                <button className="btn btn-danger orders__btn" onClick={this.deleteOrders}>Delete</button>
                            </div>

                        </div>
                    </div>
                    <div className="alert alert-danger" role="alert" hidden={this.state.hideAlert}>
                        { this.state.errMsg }
                    </div>
                    <table className="table table-bordered">
                        <thead className="thead-light">
                            <tr>
                                <th>
                                    <div className="form-group">
                                        <div className="custom-control custom-checkbox">
                                            <input className="custom-control-input" id={ "customCheckAll" } type="checkbox" checked={this.state.checkAll} onChange={(event) => this.changeCheckboxValue(-1, event)} />
                                            <label className="custom-control-label" htmlFor={ "customCheckAll" }></label>
                                        </div>
                                    </div>
                                </th>
                                <th>Customer</th>
                                <th>Order #</th>
                                <th>Order</th>
                                <th>Price</th>
                                <th className='sorting-columns' onClick={() => this.sortOrders(this.state.orders, !this.state.sortingDates.placingDate, 'placingDate')}>Placement Date</th>
                                <th className='sorting-columns' onClick={() => this.sortOrders(this.state.orders, !this.state.sortingDates.deliveryDate, 'deliveryDate')}>Delivery Date</th>
                                <th>Status</th>
                                <th>Survey Sent</th>
                                <th>Survey Response</th>
                                <th>Edit</th>
                                <th>Receipt</th>
                            </tr>
                        </thead>
                        <tbody>
                            {
                                this.state.ordersShown.map((order, index) => {
                                    return(
                                        <tr key={ index } style={{ backgroundColor:  Number(order.discountPercentage) !== 0? `rgb(${Number(order.discountPercentage) + 155}, 10, 0)` : "#fff", color: Number(order.discountPercentage) !== 0? '#fff' : '#000' }}>
                                            <td>
                                                <div className="form-group">
                                                    <div className="custom-control custom-checkbox">
                                                        <input className="custom-control-input" id={ "customCheck" + index } type="checkbox" checked={order['toDelete']} onChange={(event) => this.changeCheckboxValue(index, event)} />
                                                        <label className="custom-control-label" htmlFor={ "customCheck" + index }></label>
                                                    </div>
                                                </div>
                                            </td>
                                            <td>{ order.customerId }</td>
                                            <td>{ order.orderNumber }</td>
                                            <td>
                                                {
                                                    order.orderDetails.map((orderDetail, i) => {
                                                        return(
                                                            <div key={ i }>{ orderDetail.quantity } x { orderDetail.code }</div>
                                                        )
                                                    })
                                                }
                                            </td>
                                            <td>{ order.price } { Number(order.discountPercentage) !== 0? '(' + (order.discountPercentage) + '%)' : '' }</td>
                                            <td>{ new Date(order.placingDate).toLocaleString() }</td>
                                            <td>{ order.deliveryDate === null || order.deliveryDate === ''? '' : new Date(order.deliveryDate).toLocaleString() }</td>
                                            <td>{ order.status }</td>
                                            <td>{ order.surveySent ? 'yes' : 'no' }</td>
                                            <td>{ order.surveyResponse ? 'yes' : 'no' }</td>
                                            <td>
                                                <button className="btn btn-info"  onClick={() => this.editOrder(order)}>Edit</button>
                                            </td>
                                            <td>
                                                <button className="btn btn-info"  onClick={() => this.sendEmail(order)}>Send</button>
                                            </td>
                                        </tr>
                                    )
                                }) 
                            }
                        </tbody>
                    </table>
                    {/* <button >Placement Date</button> */}
                    <Paginator items={this.state.filteredOrders.length > 0? this.state.filteredOrders : this.state.orders} itemsPerPage={this.state.itemsPerPage} handleItems={this.handleOrdersShown} />
                    <OrderEdit showModal={this.state.showModal} order={this.state.orderToEdit} closeModal={this.closeModal} updateOrder={this.updateOrder}></OrderEdit>
                    <OrderInsert showModalInsert={this.state.showModalInsert} closeModal={this.closeModal} getOrders={this.getAllOrders}></OrderInsert>
                    <Receipt showEmailModal={this.state.showEmailModal} order={this.state.orderToSend} closeModal={this.closeEmailModal}></Receipt>
                </div>
            </div>
        );
    }
}